body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.fc-right .fc-prev-button, .fc-right .fc-next-button{
  background-color: #b1d583;
  background-image: none;
}
.dialog__button {
  box-sizing: content-box;
  color: #d6d6d6;
  background: white;
  position: absolute;
  border: 1px solid #d6d6d6;
  border-radius: 50%;
  height: 20px;
  right: 7px;
  top: 6px;
  font-family: 'Source Sans Pro', sans-serif;
}

.MuiDialogActions-root {
  justify-content: space-between !important;
}

#button__Save {
  color: #6A6996 !important;
}

.MuiButton-root {
  text-transform: none !important;
}

.MuiButton-label {
  color: #ff5f5f;
  font-family: 'Source Sans Pro', sans-serif;
}

.MuiDialogActions-spacing > * + * {
  margin-right: 9px;
  margin-left: 8px;
}

.button__Save {
  border: none;
  background: none;
  color: #6a6996;
  font-size: 15px;
  cursor: pointer;
  font-family: 'Source Sans Pro', sans-serif;
}

.MuiDialogContent-root:first-child {
  width: 201px;
}


.body {
    margin: 0;
    padding: 0;
  }
  .header {
    margin: 0 auto;
    padding: 0 auto;
    min-width: 1750px;
    align-items: center;
    height: 70px;
    background: #fff;
    grid-template-columns: 34% 5% 4% 3% 0% 5% 3% 5%;
    grid-template-rows: 1fr;
    display: grid;
  }
  
  .header__search {
    width: 16px;
    height: 16px;
    cursor: pointer;
  }
  
  .n__header__input {
    border: none;
    width: 300px;
    padding: 0px 11px;
    cursor: pointer;
  }
  
  .header__search__input {
    padding: 0px 11px;
    cursor: pointer;
  }
  
  .header__avatar {
    width: 38px;
    height: 38px;
    cursor: pointer;
  }
  
  .header__planet {
    width: 16px;
    height: 16px;
    opacity: 1;
    cursor: pointer;
  }
  
  .header__name {
    display: inline-grid;
    margin: 0;
    cursor: pointer;
  }
  
  @media (min-width: 768px) {
    .header {
      max-width: 720px;
    }
  }
  
.fc-toolbar h2 {
  content: 'Dec 31 - Jan 6';
  font-family: 'Source Sans Pro', sans-serif;
  font-size: 18px;
  margin: 0;
  color: #4d4f5c;
  margin-top: 64px !important;
}

.fc-left {
  margin-top: 64px !important;
}

.fc-toolbar.fc-header-toolbar {
  height: 109px;
  grid-template-columns: 50% 50%;
}

.fc-button-primary {
  color: #707070;
  background-color: #fff;
  border-color: #d7dae2;
}

.fc-button-primary:disabled {
  font-family: 'Source Sans Pro', sans-serif;
  color: #3b86ff;
  background-color: rgb(255, 255, 255);
  border-color: #2C3E50;
}

.fc-button-primary:not(:disabled):active, .fc-button-primary:not(:disabled).fc-button-active {
  background-color: #fff;
  color: #3b86ff;
  border-color: #d7dae2;
}

.fc-right {
  margin-bottom: 14px;
  background-image: none;
}

.fc th { 
  color: #a3a6b4;
  font-size: 11px;
}

.fc-unthemed td.fc-today {
  background: #eAf0f4;
}

.fc-ltr .fc-dayGrid-view .fc-day-top .fc-day-number {
    color: #43425d;
}

.main__calendar {
  position: absolute;
  font-family: 'Source Sans Pro', sans-serif;
  color: #4d4f5c;
}

.fc-unthemed th, .fc-unthemed .fc-list-heading td {
   height: 45px;
   background: #EAF0F4;
}
.fc th {
  border-style: none;
  vertical-align: inherit;
}

.fc-unthemed tbody {
  vertical-align: top;
}

